
.home{
    height: 100vh;
}
.home-container {
  background-image: url("../Image/background.jpg");
  background-repeat: none;
  background-size: cover;
  background-attachment: fixed;
  position: fixed;
  z-index: -2;
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}
.header-home{
    padding-bottom: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}
.home-content {
    -webkit-transform: rotate(-21deg);
        -ms-transform: rotate(-21deg);
            transform: rotate(-21deg);
    margin-top: 200px;
    margin-left: 190px;
}
.pic{
    width: 270px;
    -webkit-transform: rotate(-21deg);
        -ms-transform: rotate(-21deg);
            transform: rotate(-21deg);
    margin-top:60px;
    margin-left:350px;
    -webkit-filter: grayscale(10%); 
    filter: grayscale(10%);
}
.row{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    margin-left:40px;
}


